import React, { Component } from 'react'
import Nav from './NavBar'
import bmi from '../img/bmi.jpg'
import clue from '../img/clue.jpg'
import clue1 from '../img/clue3.jpg'
import clue4 from '../img/clue4.jpg'
import '../CssFiles/body.css'
import '../CssFiles/demo.css'
import Footer from './Footer' 
import M from "materialize-css";
import HealthBody from './HealthBody'

import MobiNav from './mobileNav'
export default class Explore extends Component {
    componentDidMount() {
        // Auto initialize all the things!
        M.AutoInit();
    }
   constructor(){
       super();
this.state={
    height:'',
    weight:'',
    bmiIndex:'',
    chest:'',
    abdomin:'',
    thigh:'',
    bfpIndex:'',
    Tricep:'',
    Abdomin:'',
    Suprailiac:''
}  
 
this.submitHandler=this.submitHandler.bind(this);
this.handleHeight=this.handleHeight.bind(this);
this.handleWeight=this.handleHeight.bind(this);
this.handlebfp=this.handlebfp.bind(this);
}
handlebfp(e){
    this.setState({[e.target.name]:[e.target.value]})

}
handleHeight(e) {
        this.setState({[e.target.name]:[e.target.value]});
      }
handleWeight(e){
   this.setState({[e.target.name]:[e.target.value]}) 
}
submitHandlerbfp=(e)=>{
let chest=this.state.chest;
let abdomin=this.state.abdomin;
let thigh=this.state.thigh;
let Tricep=this.state.Tricep;
let Abdomin=this.state.Abdomin;
let Suprailiac=this.state.Suprailiac;
   let bfp=(chest*1+abdomin*1 +thigh*1)/3;
   let bfp1=(Tricep*1+Abdomin*1 +Suprailiac*1)/3; 
   if(Tricep=='' || Abdomin=='' || Suprailiac==''){
    
    this.setState({bfpIndex:bfp.toFixed(2)})
   }
   else if(chest=='' || abdomin=='' || thigh==''){
   
    this.setState({bfpIndex:bfp1.toFixed(2)})}
    e.preventDefault();

}
submitHandler=(e)=>{
    e.preventDefault();
    let height=(this.state.height);
    let weight=(this.state.weight);
    let bmi=(weight)/((height/100)*(height/100));
    bmi= Math.floor(bmi * 100) / 100;
    this.setState(()=>{{bmiIndex:bmi.toFixed(2)}})
    if(bmi>25){
        this.setState({bmiIndex:bmi.toString()+' (Overweight)'})
    }
    else if(bmi<18.5){
        this.setState({bmiIndex:bmi.toString()+' (Underweight)'})  
    }
  
    else if(bmi>=18.5 && bmi<=25)
    this.setState({bmiIndex:bmi.toString()+' (Normal)'})
console.log(bmi);
  if(this.state.height==''|| this.state.weight=='')
       alert('fields cannot be empty')
     
      
}

componentDidMount() {
    // Auto initialize all the things!
    M.AutoInit();
}
    render() {
        return (
            <div>
                <div className="hide-on-med-and-down">
                <Nav/>
                </div>
                <div className="mobile hide-on-med-and-up">
  <MobiNav/>
  </div>
                <div className="row container">
                <div className="col l12 m12 s12">
                  <p><h6>There are certain factors that can tell the entire condition of your body
                     . These factors needs to be maintained in order to live a healthy life....</h6></p> 
                   <br/>
                    <ol>
                       <h6> <li>B.M.I(BODY Mass Index)</li></h6>
                       <h6> <li>B.F.P(Body Fat Percentage)</li></h6>
                        <h6><li>H.B</li></h6>
                       <h6> <li>Platelets count</li></h6>
                       <h6> <li>Blood pressure</li></h6>
                    </ol>
                </div>
               </div>
              <div className="row">
               <div className="container col l6 m6 s12 offset-l1">
                  
                  <form ><br/>
                  <h5 className="container left">Calculate BMI and BFP</h5> 
                   <div className="container left">
                   <br/><br/> 
                   <label htmlFor="height">Height</label>
                   <input type="number" name="height" placeholder="in centimeters"  onChange={this.handleHeight}/>
                   </div>
                  
                   <div className="container left">
                   <label htmlFor="weight">Weight</label>
                   <input type="number" name="weight" placeholder="kilograms" onChange={this.handleWeight}/>
                   </div>
                   <div className="container left"> 
                       <input type="button" className="btn red darken-2 pulse" onClick={(e)=>{this.submitHandler(e)}} value="Calculate"/>
                  <input type="text" name="endIndex" placeholder="Your B.M.I is..." value={this.state.bmiIndex}/>
                   </div>
                   <br/><br/>
                   <h5 className=" container col l6 " id="bfm">Body Fat Measurments</h5>
                   <div className="col l6">
               
                   <a class=" green btn-floating btn modal-trigger pulse" href="#modal1">
                    <i className="material-icons">help</i></a>
     
                   
                   </div>
                   <br/><br/>
                   <div className="col s12">
                  
                       <ul className="tabs">
                       <li className="tab col s3"><a href="#test1">Female</a></li>
                        <li className="tab col s3"><a class="active" href="#test2">Male</a></li>
                       </ul>
                    
                   </div>
                   <div id="test2">
                   <div className="container left">
                   <label htmlFor="chest">Chest
                      
                   </label>
                   <input type="number" name="chest" placeholder="mm" onChange={this.handlebfp}/>
                   </div>
                   <div className="container left">
                   <label htmlFor="abdomin">Abdomin</label>
                   <input type="number" name="abdomin" placeholder="mm" onChange={this.handlebfp}/>
                   </div>
                   <div className="container left">
                   <label htmlFor="thigh">Thigh</label>
                   <input type="number" name="thigh" placeholder="mm" onChange={this.handlebfp}/>
                   </div>
                   </div>
                   <div id="test1">
                   <div className="container left">
                   <label htmlFor="Tricep">Tricep</label>
                   <input type="number" name="Tricep" placeholder="mm" onChange={this.handlebfp}/>
                   </div>
                   <div className="container left">
                   <label htmlFor="abdomin">Abdomin</label>
                   <input type="number" name="Abdomin" placeholder="mm" onChange={this.handlebfp}/>
                   </div>
                   <div className="container left">
                   <label htmlFor="Suprailiac">Suprailiac</label>
                   <input type="number" name="Suprailiac" placeholder="mm" onChange={this.handlebfp}/>
                   </div>
                   
                   </div>
                   <div className="container left"> 
                       <input type="button" className="btn cyan darken-2 pulse" onClick={(e)=>{this.submitHandlerbfp(e)}} value="Calculate"/>
                  <input type="text" name="endIndex" placeholder="Your B.F.P is..." value={this.state.bfpIndex}/>
                   </div>
                 
                   </form>

               </div>
               <br/>
               <div className="container pull-l1 col l5 m4 s12">
                   B.M.I
                   <img className="bmi" src={bmi}/>
               </div>
               <br/><br/>
               <div className="container bmi1 pull-l1 col l5 m4 s12">
                   B.F.M
                   <img className="bmi" src={clue}/>
               </div>
              
               </div>
               <div className="container row">
               <div className="col l4 container haemoglobin">
                  
                   <h5>Haemoglobin(hb)</h5>
                   <ol>
                     <h6> <li>
                       For men, 13.5 to 17.5 grams per deciliter   </li><br/>
                       <li>For women, 12.0 to 15.5 grams per deciliter</li>
                       </h6> 
                   </ol>
                   <br/>
               </div>
               <div className=" hb-data container col l8">
                   <p>
A hemoglobin test measures the amount of hemoglobin in your blood. 
Hemoglobin is a protein in your red blood cells that carries oxygen 
to your body's organs and tissues and transports 
carbon dioxide from your organs and tissues back to your lungs.

If a hemoglobin test reveals that your hemoglobin level
 is lower than normal, it means you have a low red blood
  cell count (anemia). Anemia can have many different causes,
   including vitamin deficiencies, bleeding and chronic diseases.

If a hemoglobin test shows a higher than normal level, there are 
several potential causes â€” the blood disorder polycythemia vera, living at a
 high altitude, smoking and dehydration.</p>
               </div>
               <div className="container col l6">
                   <h6><b>Lower than normal results</b></h6>
                   <p>If your hemoglobin level is lower than normal, you have anemia.
                        There are many forms of anemia, each with different causes, which can include:
                        </p>
                        <ol>
                            <li>Iron deficiency</li>
                            <li>Vitamin B-12 deficiency</li>
                            <li>Folate deficiency</li>
                             <li>Cancers that affect the bone marrow, such as leukemia</li>
                            <li>Hypothyroidism</li>
                            <li>Thalassemia â€” a genetic disorder that causes low levels of hemoglobin and red blood cells</li>
                        </ol>
               </div>
               <div className="container col l6">
                   <h6><b>Higher than normal results</b></h6>
                   <p>If your hemoglobin level is higher than normal, it may be the result of</p>
                        <ol>
                            <li>Lung disease</li>
                            <li>Dehydration</li>
                            <li>Living at a high altitude</li>
                             <li>Heavy smoking</li>
                            <li>Extreme physical exercise</li>
                            <li>Burns</li>
                        </ol>
               </div>
               <div className="popup">
             


<div id="modal1" class="modal modal-fixed-footer">
  <div class="modal-content">
    <h4 className="container">B.F.M Guide</h4><br/><br/>
    <h6 className="container l12 m12 s12"><b>Measurement Tips for the Accu-Measure Body Fat Caliper</b></h6><br/><br/>
    <p className="container col l6 m6">
        <ol className="list1">
        <li>Take all measurements on the right side of your body.</li>
        <li>To grab the skinfold easily and correctly, make sure your skin is dry and lotion free.</li>
        <li>Do not take measurements after physical activities or when you are overheated. The added fluid under the skin may increase skinfold thickness.
</li>
        <li>If you are female, avoid measuring during the menstrual cycle when there is no significant and/or noticeable weight gain.</li>
       
    </ol></p>
    <p className="container col l6 m6">
        <ol>
            <li>Measurements should not be taken on broken, damaged or unhealthy skin.</li>
            <li>If you are obese, it is not recommended that you take skinfold measurements. It is also useful to 
                take tape measurements with a body tape measure such as the MyoTape. </li>
            <li>Always use the same Accu-Measure caliper, and take the readings at the same time of day,
                 for consistent monitoring of body composition changes</li>
           
            
        </ol>
    </p>
    <img className="bmi col l6 s6 m6 compact nowrap pull-l1" src={clue1}/>
    <img className="bmi col l6 s6 m6 compact nowrap" src={clue4}/>
  </div>
  <div className="modal-footer">
    <a href="#bfm" className="modal-close waves-effect modal-trigger waves-green btn-flat">Got It</a>
  </div>
</div>


               </div>
               </div>
               <HealthBody/>
              <Footer/>
            </div>
        )
    }
}
